nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 4rem;
    display: flex;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
    gap: 0.8rem;
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(94, 94, 94, 0.3);
}

nav a:active {
    background: var(--color-active);
    color: var(--color-active);
}

.message {
    font-size: 0.7rem;
    text-align: center;
    color: var(--color-light);
}